import React from 'react'
import styled from 'styled-components'
//import Img from 'gatsby-image';

import theme from '@style/theme'
import { Button, H2, H3, Form, Grid2_3 } from '@style/global'
import Collapse from './components/collapse';
import Calendar from './components/calendar';


class Info extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      title: null,
      subTitle: null,
      speakerName: null,
      speakerInfo: null,
      hostName: null,
      hostInfo: null,
      commentatorName: null,
      commentatorInfo: null,
      abtTalk: null,
      abtSpeaker: null,
      time: null,
      place: null,
      organizer: null,
      coOrganizer: null,
      url: null,
      file: null,
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    const target = event.target;
    const value = target.value;
    const name = target.name;
    
    this.setState(
      {
        [name]: value
      }
    );
    console.log(this.state)
  }

  handleSubmit(event) {
    this.props.parentCallback(this.state);
    event.preventDefault();
  }

  render() {
    return (
      <>
        <H3><b>Poster Creator</b><br/><small>for Sociology Talks, Department of Sociology @NTU</small></H3>
        <Form onSubmit={this.handleSubmit}>
          <InputBox>
            <Collapse title="Topic" key="Topic" open='true'>
              <InputItem>
                <label for="title">Title</label>
                <input id="title" type="text" name="title" value={this.state.title} onChange={this.handleChange}/>
              </InputItem>
              <InputItem>
                <label for="subTitle">Sub-title</label>  
                <input id="subTitle" type="text" name="subTitle" value={this.state.subtitle} onChange={this.handleChange}/>
              </InputItem>
            </Collapse>
            <Collapse title="Speaker & Host" key="Speaker & Host">
              <InputItem>
                <label>Speaker</label>
                <Grid2_3>
                <input type="text" name="speakerName" value={this.state.speakerName} onChange={this.handleChange} placeholder="Name"/>
                <input type="text" name="speakerInfo" value={this.state.speakerInfo} onChange={this.handleChange} aria-label="Speaker Info" placeholder="Info"/>
                </Grid2_3>
              </InputItem>
              <InputItem>
                <label>Host</label>
                <Grid2_3>
                <input type="text" name="hostName" value={this.state.hostName} onChange={this.handleChange} placeholder="Name"/>
                <input type="text" name="hostInfo" value={this.state.hostInfo} onChange={this.handleChange} aria-label="Host Info" placeholder="Info"/>
                </Grid2_3>
              </InputItem>
            </Collapse>
            <Collapse title="Commentator" key="Commentator">
              <InputItem>
                <label>Commentator</label>
                <Grid2_3>
                  <input type="text" name="commentatorName" value={this.state.commentatorName} onChange={this.handleChange} placeholder="Name"/>
                  <input type="text" name="commentatorInfo" value={this.state.commentatorInfo} onChange={this.handleChange} aria-label="Commentator Info" placeholder="Info"/>
                </Grid2_3>
                
              </InputItem>
            </Collapse>
            <Collapse title="About" key="About">
              <InputItem>
                <label>About The Talk</label>
                <input type="text" name="abtTalk" value={this.state.abtTalk} onChange={this.handleChange}/>
              </InputItem>
              <InputItem>
                <label>About The Speaker</label>
                <input type="text" name="abtSpeaker" value={this.state.abtSpeaker} onChange={this.handleChange}/>
              </InputItem>
            </Collapse>
            <Collapse title="Time & Place" key="Time & Place" overflowWhenOpen="visible">
              <InputItem>
                <label>Time</label>
                <Calendar/>
              </InputItem>
              <InputItem>
                <label>Place</label>
                <select onChange={this.handleChange}>
                  <option value="Room 401">Room 401</option>
                  <option value="Room 101">Room 101</option>
                  <option selected value="Room 319">Room 319</option>
                  <option value="Room 318B">Room 318B</option>
                </select>
              </InputItem>
            </Collapse>
            <Collapse title="Organizer" key="Organizer">
              <InputItem>
                <label>Main Organizer</label>
                <input type="text" name="organizer" value={this.state.organizer} onChange={this.handleChange}/>
              </InputItem>
              <InputItem>
                <label>Co-organizer</label>
                <input type="text" name="coOrganizer" value={this.state.coOrganizer} onChange={this.handleChange}/>
              </InputItem>
            </Collapse>
            <Collapse title="Other" key="Other">
              <InputItem>
                <label>QR Code</label>
                <input type="url" name="url" value={this.state.url} onChange={this.handleChange}/>
              </InputItem>
              <InputItem>
                <label>Book Cover</label>
                <input type="file" name="file" value={this.state.file} onChange={this.handleChange}/>
              </InputItem>
            </Collapse>
          </InputBox>
          <Button Tertiary>
            <input type="submit" value="Submit"/>
          </Button>
          <Button Tertiary>
            <input type="button" value="Print"/>
          </Button>
        </Form>
      </>
    );
  }
}


class Poster extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      title: null,
      subTitle: null,
      speakerName: null,
      speakerInfo: null,
      hostName: null,
      hostInfo: null,
      commentatorName: null,
      commentatorInfo: null,
      abtTalk: null,
      abtSpeaker: null,
      time: null,
      place: null,
      organizer: null,
      coOrganizer: null,
      url: null,
      file: null
    };
  }

  handleCallback = (childData) =>{
    this.setState({
      title: childData.title, 
      subTitle: childData.subTitle,
      speakerName: childData.speakerName,
      speakerInfo: childData.speakerInfo,
      hostName: childData.hostName,
      hostInfo: childData.hostInfo,
      commentatorName: childData.commentatorName,
      commentatorInfo: childData.commentatorInfo,
      abtTalk: childData.abtTalk,
      abtSpeaker: childData.abtSpeaker,
      time: childData.time,
      place: childData.place,
      organizer: childData.organizer,
      coOrganizer: childData.coOrganizer,
      url: childData.url,
      file: childData.file,
    })
    console.log("childDataCalled:" + this.state)
  }

  render(){
    return (
      <Grid>
        <Input>
          <Info parentCallback = {this.handleCallback}/>
        </Input>
        <Output>
          <LifeView data={this.state}/>
        </Output>
      </Grid>
    )
  }
}


const LifeView = ({data}) =>  (
  <LifeViewBox>
    <H2>{data.title}</H2>
    <H3>{data.subTitle}</H3>
    <p>{data.speakerName}</p>
    <p>{data.speakerInfo}</p>
    <p>{data.hostName}</p>
    <p>{data.hostInfo}</p>
    <p>{data.commentatorName}</p>
    <p>{data.commentatorInfo}</p>
    <p>{data.organizer}</p>
    <p>{data.coOrganizer}</p>
    <p>{data.time}</p>
    <p>{data.place}</p>
    <p>{data.abtTalk}</p>
    <p>{data.abtSpeaker}</p>
  </LifeViewBox>  
);

export default Poster

const Grid = styled.div`
  height: 100vh;
  display: grid;
  grid-template-columns: 5fr 6fr;
  @media (max-width: ${theme.screen.lg}) {
  }
`

const Input = styled.div`
  padding: 50px;
  height: 100%;
  border-right: 1px solid ${theme.color.grayscale.S1000}; 
`

const InputBox = styled.div`
  margin-bottom: 30px;
`

const InputItem = styled.div`
	position:relative;
  margin-bottom:25px;
  display: grid;
  grid-template-columns: 1fr 4fr;
  grid-gap: 10px;
  label{
    color: ${theme.color.grayscale.S1000};
  }
  input{ 
    border-bottom:1px solid ${theme.color.grayscale.S500}; 
    ${theme.fontSize.sm};
    color: ${theme.color.grayscale.S1000};
    padding: 0 10px;
    ::placeholder{
      ${theme.fontSize.sm};
    }
  }
  input:focus{ 
    border-bottom:1px solid ${theme.color.primary.S400};
  }
  select {
    border: none;
    padding: 0 10px;
  }
`

const Output = styled.div`
  height: 90vh;
  border:  ${theme.color.primary.S100D} 1px solid;
  background: ${theme.color.supplementary.S100};
  margin: 30px;
`

const LifeViewBox = styled.div`
  height: 100%;
`