import styled, { css } from 'styled-components'
import theme from '@style/theme'

export const Container = styled.div`
  text-rendering: optimizeLegibility !important;
  -webkit-font-smoothing: antialiased;
  max-width: 2000px;
  width: 100%;
  margin: 0 auto;
  padding: 0 16px;

  @media (min-width: ${theme.screen.xs}) {
    max-width: 540px;
  }

  @media (min-width: ${theme.screen.sm}) {
    max-width: 720px;
  }

  @media (min-width: ${theme.screen.md}) {
    max-width: 960px;
  }

  @media (min-width: ${theme.screen.lg}) {
    max-width: 1200px;
  }

  @media (min-width: ${theme.screen.xl}) {
    max-width: 2000px;
  }

  ${props =>
    props.fluid &&
    `
    max-width: 2000px !important;
  `};
`

export const Section = styled.section`
  padding: 90px 0;
  overflow: hidden;
  a{
    transition: color .4s ease-out;
    position: relative;
    :hover {
      color: ${theme.color.secondary.S900};
      right: 0;
      text-decoration: none;
    }
    
    :before {
      border-bottom: 0.3em solid ${theme.color.supplementary.S300};
      content: "";
      position: absolute;
      z-index: -1;
      right: 100%;
      bottom: .14em;
      left: 0;
      right: 0;
      transition: all .4s cubic-bezier(0,.5,0,1),
                  border-color .4s ease-out;
    }

    :hover:before {
      border-bottom: 1em solid;
      border-color: ${theme.color.supplementary.S400};
    }
    
    :hover:after {
      right: 0;
    }
  }
  small {
    ${theme.fontSize.xs};
  }
`

